import React from 'react';
import css from './createShipping.style.css';
import Logo from '../../assets/box.png';
import truck from '../../assets/truck.svg';
import ship from '../../assets/ship.png';
import plane from '../../assets/plane.webp';
import {faArrowLeft, faKey, faPlaneDeparture, faShip, faTruck, faUser} from "@fortawesome/free-solid-svg-icons";
import {faCalendarAlt} from "@fortawesome/free-solid-svg-icons";
import {faQuestionCircle} from "@fortawesome/free-solid-svg-icons";
import {faUserCircle} from "@fortawesome/free-solid-svg-icons";
import {faStar} from "@fortawesome/free-solid-svg-icons";
import {faSearch} from "@fortawesome/free-solid-svg-icons";
import {faHistory} from "@fortawesome/free-solid-svg-icons";
import {faCog} from "@fortawesome/free-solid-svg-icons";
import {FontAwesomeIcon} from "@fortawesome/react-fontawesome";
import box from "../../assets/box.png"
import recargar from "../../assets/recargar.png"
import circle from "../../assets/circle.png"
import cuadros from "../../assets/cuadros.png"
import {Script, Button, Container, Row, Col, Form, Image, Card, Spinner, Collapse} from "react-bootstrap";
import SideNav, {Toggle, Nav, NavItem, NavIcon, NavText} from '@trendmicro/react-sidenav';
import '@trendmicro/react-sidenav/dist/react-sidenav.css';
import "./createShipping.style.css"
import Sticky from "react-sticky-el";
import SideBar from "../../components/side-bar/SideBar";
import Tilt from "react-tilt";
import InputForm from "../../components/inputs-form/InputForm.component";
import EstadoEnvio from "../../components/estado-envio/EstadoEnvio";

class CreateShipping extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            open: false
        }
    }

    multiselect = (id) => {
        document.getElementById("buttonTruck").style.border = "3px solid #845ec2";
        document.getElementById("buttonShip").style.border = "3px solid #845ec2";
        document.getElementById("buttonPlane").style.border = "3px solid #845ec2";
        document.getElementById(id).style.border = "5px solid #7d3c98";
    }

    render() {
        return (
            <div className={"containerShipping"}>
                <SideBar handler={this.handleSelect} data={this.props.sideBarData}/>
                <Container className={"w-100 h-100"}>
                    <Row className={"h-100 w-100 justify-content-center"}>
                        <Col md={6} className={"align-self-center d-none d-md-block"}>
                            <Row className={"justify-content-center"}>
                                <h1 className="display-4 mb-0" style={{fontSize: "50px"}}>Where do you want your Fast
                                    Rabbit to go today?</h1>
                                <div>
                                    <Tilt className="Tilt mt-4" options={{max: 25}}>
                                        <Image className="img_box" src={Logo} fluid/>
                                    </Tilt>
                                </div>
                            </Row>
                        </Col>
                        <Col xs={12} sm={6} className={"align-self-center"}>
                            <Row className={"justify-content-center"}>
                                <Card className="p-3 w-100 mr-2 shadow-lg" style={{borderRadius: "35px"}}>
                                    <Form className="mt-3">
                                        <h3 style={{color:"#845ec2"}}>Tipo de Envio:</h3>
                                        <Row className={"mt-4"}>
                                            <Col xs={4} className="text-center colBot">
                                                <Button variant={"none"}  id="buttonTruck" style={{backgroundColor:"#fff", border:"2px solid #845ec2"}}>
                                                    <FontAwesomeIcon icon={faPlaneDeparture} style={{fontSize:"3rem", color:"#845ec2"}}/>
                                                </Button>
                                            </Col>
                                            <Col xs={4} className="text-center colBot">
                                                <Button  id="buttonShip" style={{backgroundColor:"#fff", border:"2px solid #845ec2"}}>
                                                    <FontAwesomeIcon icon={faTruck} style={{fontSize:"3rem", color:"#845ec2"}}/>
                                                </Button>
                                            </Col>
                                            <Col xs={4} className="text-center colBot">
                                                <Button  id="buttonPlane" style={{backgroundColor:"#fff", border:"2px solid #845ec2"}}>
                                                    <FontAwesomeIcon icon={faShip} style={{fontSize:"3rem", color:"#845ec2"}}/>
                                                </Button>
                                            </Col>
                                        </Row>
                                        <div key={`inline-radio`} className="mt-4">
                                            <Form.Check inline label="Envio Nacional" type={"radio"}
                                                        id={`inline-radio-1`}/>
                                            <Form.Check inline label="Envio Internacional" type={"radio"}
                                                        id={`inline-radio-2`}/>
                                        </div>
                                        <Form.Group className={"mt-2"} controlId="exampleForm.ControlSelect1">
                                            <Form.Label style={{fontSize: "20px"}}>Origen</Form.Label>
                                            <Form.Control size="sm" as="select">
                                                <option>1</option>
                                                <option>2</option>
                                                <option>3</option>
                                                <option>4</option>
                                                <option>5</option>
                                            </Form.Control>
                                        </Form.Group>
                                        <Form.Group controlId="exampleForm.ControlSelect1">
                                            <Form.Label style={{fontSize: "20px"}}>Destino</Form.Label>
                                            <Form.Control size="sm" as="select">
                                                <option>1</option>
                                                <option>2</option>
                                                <option>3</option>
                                                <option>4</option>
                                                <option>5</option>
                                            </Form.Control>
                                        </Form.Group>
                                        <Row>
                                            <Col xs={12} md={4}>
                                                <InputForm type="number" placeholder="Ancho" icon={faUser}
                                                           handler={this.handleChangeName}/>
                                            </Col>
                                            <Col xs={12} md={4}>
                                                <InputForm type="number" placeholder="Largo" icon={faKey}
                                                           handler={this.handleChangePass}/>
                                            </Col>
                                            <Col xs={12} md={4}>
                                                <InputForm type="number" placeholder="Alto" icon={faKey}
                                                           handler={this.handleChangePass}/>
                                            </Col>
                                        </Row>
                                        <Button variant="none" className="reg w-100"
                                                onClick={() => this.setState({open: !this.state.open})}
                                                aria-controls={"colapse_create"}
                                                aria-expanded={this.state.open}>
                                            Cotizar </Button>

                                        <Collapse in={this.state.open} className={"mt-2"}>
                                            <div id={"colapse_create"}>
                                                <hr/>
                                                <h1>as</h1>
                                            </div>
                                        </Collapse>
                                    </Form>
                                </Card>
                            </Row>
                        </Col>
                    </Row>
                </Container>
            </div>
        );
    }
}


export default CreateShipping;
